import ErmjBaseView from "./ErmjBaseView";

export default class ErmjLeftTipsView extends ErmjBaseView{
    private leftLbl: cc.Label;
    private nLeftCount: number = 0;
    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.leftLbl = this.getComponent("leftLbl", cc.Label);
        this.leftLbl.string = "";
    }

    public updateLeftLbl(nCount: number){
        this.nLeftCount = nCount;
        this.leftLbl.string = String(nCount);
    }

    public reduceLeftLbl(dev: number){
        this.updateLeftLbl(this.nLeftCount - dev);
    }

    public clearByRound(){
        this.leftLbl.string = "0";
        this.active = false;
    }
}